body {
  background-color: white; /* #282c34;*/
  color: black;
  font-size: 18px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-decoration: none;
}





h1{
  color: rgb(0, 0, 0);
 text-align: center;
 padding: 30px;
 font-size: 60px
}

h2{
  color: rgb(0, 0, 0);
 text-align: center;
 padding: 30px;
 font-size: 50px;
}

h3{
  color: rgb(0, 0, 0);
 text-align: center;
 padding: 30px;
 font-size: 40px;
}



/*CONTENT*/

.content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: 'a b'
                       'c b';
  grid-gap: 40px;
  padding: 40px;
  
}


header {

  grid-area: b;
  padding: 2px 20px 20px 20px;
  text-transform: uppercase;
  font-size: 50px;
  letter-spacing: 2px;
  background-color: #282c34;
  color: white;

}



form {

  text-align: justify;
  background-color: #f8f9fa;
  color: rgb(0, 0, 0);
  padding: 0px 30px 30px 30px;
  justify-items: center;

}

.articles {
  display: inline-grid;
  color: rgb(0, 0, 0);
  background-color: rgb(250, 243, 250);
  padding: 0px 30px 30px 30px;
  text-align: justify;
  justify-items: center;

}


